@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  background: -webkit-gradient(linear, left top, right top, from(#69b7eb), color-stop(#b3dbd3), to(#f4d6db));
  background: linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);
  font-size: 1.1rem;
  font-family: "Quicksand", sans-sans-serif;
  height: 100%;
}

.Todo {
  display: flex;
  margin: 0 -3rem 4px;
  padding: 1.1rem 3rem;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
}

.Todo-task {
  position: relative;
  -webkit-transition: opacity 0.4s linear;
  transition: opacity 0.4s linear;
}

.Todo-task.completed {
  opacity: 0.6;
}

.Todo-task.completed::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -0.5rem;
  display: block;
  width: 0%;
  height: 2px;
  background: white;
  -webkit-animation: strikeitem 0.5s ease-out 0s forward;
          animation: strikeitem 0.5s ease-out 0s forward;
}

@-webkit-keyframes strikeitem {
  to {
    width: calc(100% + 1rem);
  }
}

@keyframes strikeitem {
  to {
    width: calc(100% + 1rem);
  }
}

.Todo-buttons {
  flex-shrink: 0;
  padding-left: 0.7em;
}

.Todo-buttons button {
  background: none;
  border: none;
  font-size: 1em;
  margin: 0.4em;
  -webkit-appearance: none;
  cursor: pointer;
  color: white;
}

.Todo-edit-form {
  display: flex;
  flex-wrap: wrap;
}

.Todo-edit-form input{
  flex-grow: 1;
  border: none;
  background: white;
  padding: 0 1.5em;
  font-size: medium;
  font-size: initial;
}

.Todo-edit-form button{
  padding: 0 1.3rem;
  border: none;
  background: #ff6666;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin-left:5px;
  cursor: pointer;
  -webkit-transition: background 0.2s ease-out;
  transition: background 0.2s ease-out;
}

.Todo-edit-form button:hover {
  background: #ff5e5e;
}

.Todo-edit-form input,
.Todo-edit-form button {
  font-family: 'Quicksand', sans-serif;
  height: 3rem;
}

.NewTodoForm {
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap;
}

.NewTodoForm input {
  flex-grow: 1;
  border: none;
  background: white;
  padding: 0 1.5em;
  font-size: medium;
  font-size: initial;
}

.NewTodoForm button{
  padding: 0 1.3rem;
  border: none;
  background: #ff6666;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin-left:5px;
  cursor: pointer;
  -webkit-transition: background 0.2s ease-out;
  transition: background 0.2s ease-out;
}

.NewTodoForm button:hover {
  background: #ff5e5e;
}

.NewTodoForm input,
.NewTodoForm button {
  font-family: 'Quicksand', sans-serif;
  height: 3rem;
}

.TodoList {
  margin: 4rem auto;
  padding: 2rem 3rem 3rem;
  max-width: 500px;
  background: #ff6666;
  color: white;
  box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, 0.1);
}

.TodoList ul {
  margin-top: 2.6rem;
  list-style: none;
}

.TodoList h1 {
  font-weight: normal;
  font-size: 2.6rem;
  letter-spacing: 0.05em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.TodoList h1 span {
  display: block;
  font-size: 0.8rem;
  margin-bottom: 0.7rem;
  margin-left: 3px;
  margin-top: 0.2rem;
}

